body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: black;
  color: white;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100vw;
  line-height: 2.5em;
}

.hero {
  background: url("http://res.cloudinary.com/bfrest/image/upload/e_blur:300,q_100/v1525544207/DJI_0785.jpg");
  height: 100vh;
  width: 100vw;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  -webkit-animation: fadeIn linear 0.5s;
          animation: fadeIn linear 0.5s;
}

h1 {
  font-size: 5em;
  text-shadow: 2px 2px 4px black;
  -webkit-animation: fadeInNGrow linear 0.5s;
          animation: fadeInNGrow linear 0.5s;
}

.gallery-btn {
  width: 6%;
  border-radius: 10px;
}

@media (max-width: 450px) {
  .hero {
    height: 50vh;
  }

  h1 {
    font-size: 2em;
  }
}

@-webkit-keyframes fadeInNGrow {
  from {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes fadeInNGrow {
  from {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.service-component-wrapper {
  width: 100vw;
  display: flex;
  color: white;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.service-component-wrapper p {
  text-align: left;
}

.service-wrapper {
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  align-content: center;
  justify-items: center;
}

.service-header {
  font-size: 3em;
}

.service-item {
  width: 70%;
  margin: 20px;
  padding: 10px;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.4em;
}

.service-icon {
  width: 30%;
}

@media (max-width: 450px) {
  .service-wrapper {
    grid-template-columns: 1fr;
  }
  .service-header {
    font-size: 2em;
  }

  .service-item {
    font-size: 1.2em;
    text-align: left;
    text-align: initial;
  }
}

.contact-component-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: white;
  padding-top: 10%;
  padding-bottom: 5%;
}

.contact-form {
  display: flex;
  flex-direction: column;
  width: 50%;
}

input {
  font-size: 1.5em;
  text-align: center;
  border: none;
  align-self: center;
  width: 100%;
  margin-bottom: 3%;
  border: 2px solid black;
  border-radius: 5px;
}

input:focus {
  border: 2px solid yellow;
}

textarea {
  font-size: 1.5em;
  height: 20vh;
  width: 100%;
  margin-top: 2%;
  margin-bottom: 3%;
  border: 2px solid black;
  border-radius: 5px;
}

textarea:focus {
  border: 3px solid yellow;
}

button {
  background: none;
  color: white;
  border: 2px solid white;
  font-size: 1.5em;
  width: 40%;
  align-self: center;
  padding: 5px;
}

button:hover {
  border: 2px solid yellow;
  color: yellow;
}

@media (max-width: 450px) {
  .contact-form {
    width: 80%;
    font-size: 0.8em;
    margin-bottom: 50px;
  }

  input {
    width: 94%;
  }

  text-area {
    font-size: 1em;
  }
}

